import React from "react";
import FTW from "../../packages/ftw/nlp2";
import { CurrentWalletProps, InvokeScript } from "../../packages/wallet/types";
import { toast } from "react-toastify";

const Play = ({
  currentWallet,
  openInvoker,
}: {
  currentWallet?: CurrentWalletProps;
  openInvoker: (invokeScript: InvokeScript) => void;
}) => {
  console.log("Render: Scene/Play");
  const handleSubmit = (numbers: number[]): void => {
    if (currentWallet) {
      let args = [currentWallet.address, 1, 1, ...numbers];
      if (process.env.REACT_APP_REFERRAL_ADDRESS) {
        args.push(process.env.REACT_APP_REFERRAL_ADDRESS);
      }
      openInvoker(FTW.getInvokeScript.buy(args));
    } else {
      toast("Connect your wallet");
    }
  };

  return (
    <div className="box">
      <h1 className="title is-spaced is-4">N3 Migration</h1>
      <p className="subtitle is-6">
        We launched our first game in 2018. We are so proud our purely
        decentralized game could serve the best dapp UX. We've preparing the
        migration since 2019. We will introduce something cool in N3. Stay
        tuned!
      </p>
      <a href={"https://www.forthewin.network"}>forthewin.network</a>
      {/*<div className="box is-hidden-tablet">*/}
      {/*  /!*<LotteryCard hideDrawing={true} />*!/*/}
      {/*</div>*/}
      {/*<NumberPicker*/}
      {/*  onBuy={selectedNumbers => handleSubmit(selectedNumbers)}*/}
      {/*  maxNumber={39}*/}
      {/*  totalBalls={5}*/}
      {/*/>*/}
      {/*<LotteryAgreement />*/}
    </div>
  );
};

export default Play;
